











































































































































































import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
  import PartyMember from "@/components/PartyMember.vue";
  import {Member} from "@/data/Member";
import {NewMember} from "@/data/actions/NewMember";
import {Party} from "@/data/Party";
import ClassConfiguration from "@/components/ClassConfiguration.vue";
import ClassField from "@/components/ClassField.vue";
import {ShowClassesAvailable} from "@/data/actions/ShowClassesAvailable";
import {Class, Classes, Tier} from "@/data/classes";
import RecruitRequirements from "@/components/RecruitRequirements.vue";
import ClassProficiencyRequirements from "@/components/ClassProficiencyRequirements.vue";
import AbilityLabel from "@/components/AbilityLabel.vue";
import {Unit, Units} from "@/data/units";
import ClassGrowth from "@/components/ClassGrowth.vue";
import SkillLabel from "@/components/SkillLabel.vue";
import UnitGrowth from "@/components/UnitGrowth.vue";

  @Component({
      components: {
          UnitGrowth,
          SkillLabel,
          ClassGrowth,
          AbilityLabel, ClassProficiencyRequirements, RecruitRequirements, ClassField, PartyMember}
  })
  export default class Planner extends Vue {
      @Prop() store!: string;

      tempStore = '';
      party: Party = {
          members: []
      };

      showGrowth = true;

      jump(i: number) {
          (this.$refs['member-'+i] as Array<HTMLElement>)[0].scrollIntoView();
      }

      reset() {
          this.party = {
              members: []
          };
      }

      addMember() {
          this.$nextTick(() => {
              this.party.members.push(NewMember());
          });
      }

      addMemberToEnd() {
          this.$nextTick(() => {
              this.party.members.unshift(NewMember());
          });
      }

      remove(index: number) {
          if (window.confirm('Remove ' + this.party.members[index][0] + '?')) {
              this.party.members.splice(index, 1);
          }
      }

      mounted() {
          this.loadFromHash();
      }

      lookupClass (selectedClass: keyof typeof Classes, attrib: (target: Class) => any): Class {
          return Classes[selectedClass] ? attrib(Classes[selectedClass]) : '';
      }

      lookupUnit (selectedUnit: keyof typeof Units, attrib: (target: Unit) => any): Unit {
          return Units[selectedUnit] ? attrib(Units[selectedUnit]) : '';
      }

      loadFromHash(newStore?: string) {
          if (newStore) {
              this.store = newStore;
          }

          if (this.store === '-') {
              this.saveToHash();
          } else {
              try {
                  this.party = JSON.parse(atob(this.store));
              } catch (e) {
                  console.warn('Unable to load from URL')
              }
          }
      }

      classes(unit: keyof typeof Units, tier: Tier) {
          return ShowClassesAvailable(unit, tier);
      }

      @Watch('store', {immediate: true})
      updateTempStore() {
          this.tempStore = this.store;
      }

      @Watch('party', {deep: true})
      saveToHash() {
        const store = JSON.stringify(this.party);
        try {
          if (store === atob(this.store)) return;
        } catch (e) {}
        this.$router.replace('/' + btoa(store));
      }

  }
